@import '_variables';
@import '_mixins';
@import '~video.js/dist/video-js.css';
@import '~videojs-dock/dist/videojs-dock.css';

$assets-path: '../../assets/';
@import '../../sass/player/index';

.standalone-video-embed {
  margin: 0 !important;
  background-color: black;
  overflow: hidden;
  
  .video-js .vjs-tech {
  }

  .video-js-wrapper {
    position: absolute;
    left: 0%;
    top: 0%;
    height: 100%;
    width: 100%;
  }

}

.vjs-volume-horizontal .vjs-volume-level,
.vjs-load-progress,
.vjs-play-progress{
  transform-origin : left;
}

video {
  width: 100%;
}

video[src], .vjs-fullscreen {
  padding: 0 !important;
}

#video-wrapper {
  width: 100%;
  height: 100%;
}

.video-js{
  background: transparent;
  font-family: 'Segoe UI', SegoeUI, "Helvetica Neue", Helvetica, Arial, sans-serif!important;

  .vjs-bezel,
  .vjs-control-bar{
    background: transparent;
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);  

    button{
      -webkit-filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
      filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
    }
    
    
  }

  .vjs-overlay{
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
    filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
  }

  .vjs-progress-control,
  .vjs-volume-control{
    .vjs-slider-bar{
      box-shadow: 1px 1px 1px rgba(0,0,0,.2);
    } 
    
  }
  
  .vjs-bezel{
    z-index: 1;
  }
  
}

.vjs-loading-spinner{
  display: none;
  z-index: 1;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px); 
}

.vjs-slider-horizontal{
  .vjs-slider-bar{
    width: 100%!important;
    transition: transform .2s;
    &:before{
      display: none;
    }
  }
}

.video-js .vjs-progress-control:hover .vjs-progress-holder{
  height: 8px;
}

.vjs-slider-vertical{
  .vjs-slider-bar{
    height: 100%!important;
    transition: transform .2s;
    &:before{
      display: none;
    }
  }
}
.vjs-volume-level{
  pointer-events: none;
  &:before{
    display: none;
  }
}
.vjs-play-progress{
  pointer-events: none;
  .vjs-time-tooltip{
    display: none!important;
  }
}

.video-js .vjs-tech {
  position: absolute;
  left : 0;
  top : 0;
  bottom : 0;
  right: 0;
}



.video-js {
  position: relative;

  

  &.vjs-fullscreen {
    video {
      max-width: none!important;
      max-height: 100%;
      /*max-height: 100%;
      top: 50%;
      transform: translateY(-50%);*/
    }

    .vjs-picture-in-picture-control{
      display: none!important;
    }
  }
}

.vjs-peertube {
  padding-left: 1em;
  line-height: 30px;
  display: none;

  .peers-number,
  .upload-speed-text,
  .download-speed-text{
    margin-right : 1em;
  }
}
/*
html.testaddress{
  .vjs-peertube{
    display: block;
  }
}*/

.vjs-peertube-hidden{
  display: none;
}

.video-js {
  width: 100%;
  overflow: hidden;
  height: 100%!important;

  video{
    background: #000;
  }

  .vjs-poster {
    background-size: 100% auto;
    left: -1px;
    top : -1px;
    bottom : -1px;
    right: -1px;
    height: auto;
  }

  @media screen and (max-width: 350px) {
    .vjs-play-control {
      padding: 0 5px !important;
      width: 25px !important;
    }

    .vjs-volume-control {
      display: none !important;
    }

    .vjs-volume-panel {
      width: 26px !important;
    }

    .vjs-peertube-link {
      padding: 0;
    }
  }
}

#placeholder-preview {
  position: absolute;
  top: 0;
  left: 0;
  background-size: 100% auto;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
}




.pocketnet-logo-video-player {
  max-width: 100%;
}

 
.js-player {
  position: relative;

  .warning-block,
  .error-block{
    height: 300px;
    display: flex;
    align-items: center;
    background: #000;
    color : rgb(190, 190, 190);

    >div{
      margin : 0 auto;
      text-align: left;
      padding : 50px;
    }
  }

  .error-reload{
    margin-top : 2em;
    cursor: pointer;
    background: rgb(26, 101, 241);
    color: #fff;
    border: 1px solid rgb(26, 101, 241);
    text-align: center;
    font-size: 0.8em;
    font-weight: 400;
    border-radius: 2px;
    padding : 0.5em 3em;

    &:hover{
      background: transparent;
      color: rgb(26, 101, 241);
    }
  }

  &[error="noncritical"]{
    .vjs-big-play-button, #vjs-forward-button, #vjs-rewind-button{
      display: none!important;
    }
    .error-block{
      position: absolute;
      left : 0;
      right : 0;
      top : 0;
      bottom : 0;
      height: auto;
      background: rgba(0,0,0,0.85);

      .error-title{
        display: none;
      }

      .error-reload{
        background: rgba(26, 101, 241);
        color: rgba(255, 255, 255);
        border: 1px solid rgba(26, 101, 241);
        text-align: center;
        font-size: 0.8em;
        font-weight: 400;
        border-radius: 2px;
        padding : 0.75em 3em;
    
        &:hover{
          background: transparent;
          color: rgba(255, 255, 255, 1);
          border: 1px solid rgba(255, 255, 255, 1);
        }
      }
    }
  }

  .warning-block{
    position: absolute;
    z-index: 20;
    left : 0;
    top : 0;
    bottom : 0;
    right : 0;
    background:rgba(0, 0, 0, .45);
    height: auto;
  }

  .warning-overlay{
    position: absolute;
    padding-left: 2em;
    z-index: 1000;
    top : 50%;
  }
}

.vjs-thumb{
  background : #000;
  background-size: 100% auto;
}

.js-player,
.video-js-wrapper {
  box-sizing: border-box;

  .vjs-menu * {
    font-family: 'Segoe UI', SegoeUI, "Helvetica Neue", Helvetica, Arial, sans-serif !important;
  }

  .vjs-menu-content {
    overflow-y: hidden;
  }

  .vjs-settings-dialog{
    background: rgb(0, 11, 58);
    border-radius: 12px;
  }

  

  .video-js .vjs-time-tooltip {
    top: -3.9em;
  }

  .video-js .vjs-control:focus,
  .video-js .vjs-control:focus:before,
  .video-js .vjs-control:hover:before {
    text-shadow: 0 0 0em #fff;
  }

  .vjs-menu li.vjs-menu-item:focus,
  .vjs-menu li.vjs-menu-item:hover {
    outline: none
  }

  .video-js.vjs-fullscreen {}

  .video-js button {
    outline: none
  }

  .video-js .pocketnet-link-button{
    width: auto;
    padding : 0 1em;

    a{
      display: block;
      background: rgba(32, 35, 42,1);
      color : rgb(255, 255, 255);
      padding : 0 1em;
      border-radius: 6px;
      height: 3em;
      line-height: 3em;
      text-decoration: none;
    }
  }

  .video-js .vjs-controls-disabled .vjs-big-play-button {
    display: none !important
  }

  .vjs-paused.vjs-has-started.video-js .vjs-big-play-button,
  .video-js.vjs-ended .vjs-big-play-button,
  .video-js.vjs-paused .vjs-big-play-button {
    display: block
  }

  .video-js .vjs-load-progress div,
  .vjs-seeking .vjs-big-play-button,
  .vjs-waiting .vjs-big-play-button {
    display: none !important
  }

  .video-js.vjs-ended .vjs-big-play-button {
    display: block !important
  }

  .video-js #vjs-forward-button, 
  .video-js #vjs-rewind-button,
  .video-js .vjs-big-play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -1.5em;
    margin-top: -1.5em;
    width: 3em;
    height: 3em;
    line-height: 3em;
    text-align: center;
    font-size: 1.3em;
    border: none;
    border-radius: 50%;
    color: #fff;
    font-size: 40px;
    -webkit-transition: border-color .4s, outline .4s, background-color .4s;
    -moz-transition: border-color .4s, outline .4s, background-color .4s;
    -ms-transition: border-color .4s, outline .4s, background-color .4s;
    -o-transition: border-color .4s, outline .4s, background-color .4s;
    transition: border-color .4s, outline .4s, background-color .4s;
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
    background: transparent;

    i{
      vertical-align: middle;
      font-size: 0.5em;
    }
  }

  .video-js #vjs-rewind-button{
    margin-left: -4.5em;
    line-height: 2.85em;
  }

  .video-js #vjs-forward-button{
    margin-left: 1.5em;
    line-height: 2.85em;
  }

  /*.video-js .vjs-big-play-button, #vjs-forward-button, #vjs-rewind-button {
    background-color: rgba(0, 0, 0, 0);
    font-size: 70px;
    border-radius: 0%;
    height: 1em !important;
    line-height: 1em !important;
    margin-top: -0.5em !important
  }

  #vjs-forward-button {
    left: 75%  
  }
  #vjs-rewind-button {
    left: 20%  
  }

  #vjs-rewind-button, #vjs-forward-button {
    position: absolute;

    button {
      width: 85px;
      display: block;
      line-height: 0px;
      padding: 18px;
    }
  }*/

  .video-js:hover .vjs-big-play-button,
  .video-js .vjs-big-play-button:focus,
  .video-js .vjs-big-play-button:active {
    background-color: rgba(0, 0, 0, 0)
  }

  .video-js .vjs-remaining-time-display {
    display: none
  }

  .video-js .vjs-duration {
    display: block;
    left: 0em
  }

  .video-js .vjs-current-time {
    display: block;
    left: 0
  }

  .vjs-live .vjs-time-control {
    display: none
  }

  .vjs-time-divider {
    display: block;
  }

  .vjs_video_3-dimensions{
    height: auto;
  }

  .pocketnet-logo-video-player .icon.icon-full-logo-transparent {
    background: url('#{$assets-path}/player/images/logoBastyon.svg') no-repeat;
    width: 100px;
    height: 15px;
    display: block;
    opacity: 0.5;
  }

  .pocketnet-logo-video-player .icon.logo-bastyon {
    background: url('#{$assets-path}/player/images/logoBastyon.svg') no-repeat;
    width: 100px;
    height: 15px;
    display: block;
    opacity: 0.5;
  }

  .pocketnet-logo-video-player .icon.icon-only-logo-transparent {
    &:hover {
      opacity: 1;

      &::before {
        color: white;
      }
    }
  }

  .pocketnet-logo-video-player {
    max-width: 100%;
    left: 1.5em;
    top: 1.5em;
    position: absolute;
  }

}


.video-js.video-js-contributor{
  .vjs-remaining-time{
    display: none;
  }

  .vjs-picture-in-picture-control{
    display: none;
  }
}
.vjs-playing {
  .vjs-big-play-button[mobilebutton="true"] {
    .vjs-icon-placeholder {
      &:before {
        content: "\f103" !important
      }
    }
  }
}
.vjs-big-play-button[mobilebutton="true"] {
  display: block !important;
  visibility: visible;
  opacity: 1;
  transition: all .3s ease-in;
}

@media screen and (max-width: 300px) {
  #error-block {
    font-size: 36px;

    #error-content {
      font-size: 14px;
    }
  }
}
html.mobileview{
  #vjs-forward-button, #vjs-rewind-button {
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease-in
  }
  .vjs-has-started {
    #vjs-forward-button, #vjs-rewind-button {
      visibility: visible;
      opacity: 1;
      transition: all .3s ease-in
    }
  }
  .vjs-user-inactive.vjs-playing {
    .vjs-big-play-button {
      visibility: hidden !important;
      opacity: 0;
      transition: all .3s ease-in
    }
    #vjs-forward-button, #vjs-rewind-button {
      visibility: hidden;
      opacity: 0;
      transition: all .3s ease-in
    }
  }
  .vjs-user-active.vjs-playing {
    .vjs-big-play-button {
      visibility: visible !important;
      opacity: 1;
      transition: all .3s ease-in
    }
  }
}
  


.video-js .vjs-play-progress:before{
  padding : 1em;
  right : -1.5em;
  top : -1.3333333em;
}

.vjs-peertube {
  line-height: 3em;
}

  .vjs-youtube {

    .vjs-volume-panel {
      position: unset !important;
      width: 0 !important
    }
    
  }

  .video-js{
    .vjs-settings-dialog {
      bottom: 5em;
    }
    
    .vjs-control-bar{
      backface-visibility: hidden;
      height: 5em !important;
      font-size: 1.25em;
      text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);

      button{
        -webkit-filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
        filter: drop-shadow(1px 1px 1px rgba(0,0,0,.5));
      }

      .vjs-mouse-display{
        display: none!important;
      }
    
      .vjs-progress-control {
        position: absolute;
        width: 100%;
        bottom: 13px;
        left : 0;
        right : 0;
        height: 4px;
        display: block;
        z-index: 1;
      }

      .vjs-current-time {
        margin-left: 10px;
      }

      .vjs-mute-control {
        height: 3em;
        line-height: 3.5em;
        font-size: 1em;
      }
      
      .vjs-icon-settings {
        position: absolute;
        right: 4em;
        width: 4em;
        top: 0;
        height: 3em;
        font-size: 1em;

        span{
          height: 1.5em;
          width: 1.5em;
        }
      }

      .vjs-picture-in-picture-control {
        position: absolute;
        right: 8em;
        width: 4em;
        top: 0;
        height: 3em;
        font-size: 1em;

        span{
          height: 1.5em;
          width: 1.5em;
        }
      }

      .vjs-fullscreen-control {
        position: absolute;
        right: 0;
        height: 3em;
        line-height: 3.5em;
        font-size: 1em;
      }

      .vjs-time-divider,
      .vjs-time-control{
        padding-left: 0.1em;
        padding-right: 0.1em;
        min-width: 0;
      }

    }

    &.vjs-fullscreen {
      .vjs-control-bar{
        font-size: 1.5em;
        background:transparent;
      }
    }
  }

@mixin mobileview () {
    .video-js{
      .vjs-control-bar{
        .vjs-play-control{
          display: none;
        }
        .vjs-volume-control {
          display: none;
        }

        .vjs-mute-control {
          position: absolute;
          right: 4em;
          height: 3em;
          line-height: 3.5em;
          font-size: 1em;
        }

        .vjs-icon-settings {
          right: 8em;
        }

        .vjs-picture-in-picture-control {
          right: 12em;
        }
      }
    }
}


html.mobileview{
  @include mobileview ();
}

@media only screen and (max-width: 768px){
  @include mobileview ();
}


@media only screen and (max-width: 768px){
  @include mobileview ();
}

.pipmini,
.pipmode{

  .js-player{
    
    .vjs-control-bar{
      backdrop-filter : none;

      >div,
      >button{
        display: none;
      }

      >button{
        &.vjs-mute-control{
          right : 0;
          bottom : 0;
          display: block;
          width: 3em;
        }
      }
    }
    #vjs-rewind-button,
    #vjs-forward-button,
    .vjs-overlay{
      display: none!important;
    }
  }
}

.pipmode{
  .js-player{

    .vjs-control-bar{
      display: none!important;
    }

    .vjs-big-play-button{
      display: none!important;
    }
  }
}